import React, { Component } from 'react';
import SellStuffTxt from './pageElements/sellStuffTxt';
import SellStuffImg from './pageElements/sellStuffImg';
import SellPageBanner from './pageElements/sellBanner';
import HotItems from './pageElements/hotItems';
import HotItemsM from './pageElements/hotItems_m';
import { motion } from 'framer-motion';
import SaleMargin from './pageElements/sellMargin';
import SaleValuables from './pageElements/sellValuables';
import SaleValuableImg from './pageElements/sellValuableImg';
import { layoutGenerator } from 'react-break';

const transition = { duration: 1.2, ease: [0.43, 0.13, 0.23, 0.96] }

const variants = {
  exit: { y: 0, opacity: 0, transition: { transition } },
  enter: { y: 0, opacity: 1, transition: { delay: 0.3, ...transition } }
};
const layout = layoutGenerator({
  mobile: 0,
  phablet: 550,
  tablet: 800,
  desktop: 801,
});


const SmallScreen = layout.isAtMost('tablet');
const OnDesktop = layout.isAtLeast('desktop');



class SellPage extends Component {
  constructor(props) {
    super(props);
    this.hotItems = React.createRef();
  }
  render() {
    return (
      <motion.div initial='exit' animate='enter' exit='exit'>
        <motion.div variants={variants}>
          <div className="sell-grid">
            <SellStuffImg />
            <SellStuffTxt />
            <SellPageBanner />
            <OnDesktop>
              <HotItems ref={this.hotItems} />
            </OnDesktop>
            <SmallScreen>
              <HotItemsM ref={this.hotItems} />
            </SmallScreen>
            <SaleMargin />
            <SaleValuables />
            <SaleValuableImg />
          </div>
        </motion.div>
      </motion.div>



    );
  }
}

export default SellPage;
